import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {BeginnerComponent} from '../app/cmps/beginner/beginner.component'
import {IntermediateComponent} from '../app/cmps/intermediate/intermediate.component'
import {AdvancedComponent} from '../app/cmps/advanced/advanced.component'
import { HomePageComponent } from './pages/home-page/home-page.component';
import { ContactMeComponent } from './pages/contact-me/contact-me.component';
import { TestLevelComponent } from './pages/test-level/test-level.component';
import { ChallengeComponent } from './pages/challenge/challenge.component';
import { WelcomeComponent } from './pages/welcome/welcome.component';

const routes: Routes = [

  { path: 'test-level/beginner' , component:BeginnerComponent},
  { path: 'test-level/intermediate' , component:IntermediateComponent},
  { path: 'test-level/advanced' , component:AdvancedComponent},
  { path: 'test-level' , component:TestLevelComponent},
  { path: 'challenge' , component:ChallengeComponent},
  { path: 'contact' , component:ContactMeComponent},
  { path: 'home' , component:HomePageComponent},
  { path: '' , component:WelcomeComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
