import { Component, OnInit } from '@angular/core';
import {PuzzleService} from '../../services/puzzle.service'

@Component({
  selector: 'app-intermediate',
  templateUrl: './intermediate.component.html',
  styleUrls: ['./intermediate.component.scss']
})
export class IntermediateComponent implements OnInit {

  puzzles = []
  count = 5
 isShowAnswer: Boolean = false
 isClicked: Boolean = false
 isFlipped: Boolean = false
 isChosen: Boolean = false
 coupon: string = ''
 score = 0


  constructor(private puzzleService: PuzzleService) { }

  ngOnInit() {
    console.log('beginner component');
    
this.puzzleService.getPuzzles().subscribe((data: any[]) => {
  console.log(data);
   this.puzzles = data
   this.coupon = this.puzzleService.makeId(6)
})
  }
  sendAnswer() {
    if (this.count === 9 && this.isClicked === true) console.log('yes');
    
    this.isClicked = true
       if (this.puzzles[this.count].answer === this.puzzles[this.count].solution.img) {
        new Audio('http://www.utc.fr/si28/ProjetsUpload/P2006_si28p004/flash_puzzle/sons/rush/crowd3.wav').play()
         this.puzzles[this.count].isCorrect = true
         this.score++
       }else {
         new Audio('https://rpg.hamsterrepublic.com/wiki-images/d/d7/Oddbounce.ogg').play()
         this.puzzles[this.count].isCorrect = false
           this.isShowAnswer = true        
          this.puzzles[this.count].img = this.puzzles[this.count].solution.img
       }
      
  }
  radioChangeHandler(event:any) {
    this.isChosen = true
    this.puzzles[this.count].img  = event.target.value
    this.puzzles[this.count].answer  = event.target.value
  }

  nextQuest() {
    this.isChosen = false
    this.isClicked = false 
    this.count++
    this.isShowAnswer = false
  }

  viewResult() {
    this.puzzles = []
    this.isFlipped = true
  }

  playAgain() {
  window.location.reload()
  }

  get totalScore() {
    if (this.score === 5) return 'üòÄ Great result! try advanced level'
    if (this.score === 4) return 'üôÇ Very nice! try advanced level'
    if (this.score === 3) return 'Not bad but you need some more practice'
    if (this.score < 3) return 'üòï Try again, you will be better next time'
  }

}
