import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent {

  constructor(private router: Router) { }

  chatOpen: boolean = false
  count: number = 0
  userMsg: string = ''
  messages: Array<string> = []
  name = ''
  autoMsgs: Array<string> = ['Would you like to contact me? write ' + '\xa0\xa0' + 'yes' + '\xa0\xa0' + 'or ' + '\xa0\xa0' + 'no',
  'please write ' + '\xa0\xa0' + 'yes' + '\xa0\xa0' + 'or ' + '\xa0\xa0' + 'no']
  showMsg: Boolean = false


  toggleChat() {
    this.chatOpen = !this.chatOpen
  }
  closeChat() {
    this.chatOpen = false
  }

  sendMsg() {
    if (!this.userMsg) return
    if (this.userMsg === 'Yes' || this.userMsg === 'yes') {
      this.chatOpen = false
      this.router.navigateByUrl("contact")
      return
    }
    if (this.userMsg === 'No' || this.userMsg === 'no') {
      this.name = JSON.parse(localStorage.getItem('name'))
      this.showMsg = true
      setTimeout(() => {
        this.chatOpen = false
      }, 2500)
      return
    } else if (this.count < 1) {
      (localStorage.setItem('name', JSON.stringify(this.userMsg)))
    }
    this.messages.push(this.userMsg)
    setTimeout(() => {
      this.messages.push(this.autoMsgs[this.count])
      this.count++
      if (this.count > 2) {
        this.chatOpen = false
        this.count = 0
        this.messages = []
      }
      this.userMsg = ''
    }, 1000)
  }
}
