import { Component, OnInit, ViewChild , AfterViewInit,ElementRef } from '@angular/core';
import emailjs, { EmailJSResponseStatus } from 'emailjs-com';
import{ init } from 'emailjs-com';
import {Router} from '@angular/router';
init("user_85Gzk36csKRW5b5fwmCrZ");

@Component({
  selector: 'app-contact-me',
  templateUrl: './contact-me.component.html',
  styleUrls: ['./contact-me.component.scss']
})
export class  ContactMeComponent implements OnInit, AfterViewInit {
name = ''
isHover: boolean = false
isSent: boolean = false
coupon: string =''

  constructor(private router: Router) { }

@ViewChild('couponinput') couponinput: ElementRef
@ViewChild('username') username: ElementRef

  ngOnInit() {
this.name = JSON.parse(localStorage.getItem('name'))
this.coupon =  JSON.parse(localStorage.getItem('coupon'))
  }
  ngAfterViewInit() {
this.couponinput.nativeElement.value = this.coupon
this.username.nativeElement.value = this.name
  }

  
  public sendEmail(e: Event) {
    e.preventDefault();
    emailjs.sendForm('service_c5u92tt', 'template_4fvzhti', e.target as HTMLFormElement,'user_85Gzk36csKRW5b5fwmCrZ' )
      .then((result: EmailJSResponseStatus) => {
        console.log(result.text);
        this.isSent = true
        setTimeout(() => {
          this.router.navigateByUrl("home")
        },4000)
      }, (error) => {
        console.log('Error:',error.text);
      });
  }

  hideLine() {
    this.isHover = false
  }
  



}
